export type WorkerVariableScope = {
    level: number;
    id: number,
    start: number,
    stop: number,
    parent: WorkerVariableScope | null
};


export type Variable = {
    id: string,
    type: string,
    decType: string,
    line: number,
    level: number,
    scope: WorkerVariableScope,
}

export class VariableScope {
    start: number
    stop: number
    id: number
    level: number
    // these values are generated for the OBJ FE (syntax processor)
    // noinspection JSMismatchedCollectionQueryUpdate
    variables: Variable[]
    scopes: VariableScope[]
    parent: VariableScope | null;

    constructor(start?: number, stop?: number, id?: number, level?: number) {
        this.start = start ? start : 0;
        this.stop = stop ? stop : -1;
        this.id = id ? id : 0;
        this.level = level ? level : 0;
        this.variables = [];
        this.scopes = [];
        this.parent = null;
    }
}

export declare type Modifier = 'PUBLIC' | "PROTECTED" | "";