import {Java8ParserListener} from "../java8/Java8ParserListener";
import {
    AmbiguousNameContext,
    AnnotationContext,
    AnnotationTypeBodyContext,
    BlockContext,
    ClassBodyContext,
    ClassDeclarationContext,
    ClassInstanceCreationExpression_lf_primaryContext,
    ClassInstanceCreationExpression_lfno_primaryContext,
    ClassInstanceCreationExpressionContext,
    ClassTypeContext, CompilationUnitContext,
    ConstructorBodyContext,
    EnumBodyContext,
    FieldDeclarationContext,
    ImportDeclarationContext,
    InterfaceBodyContext,
    InterfaceTypeContext,
    Java8Parser,
    LocalVariableDeclarationContext,
    MethodDeclarationContext,
    MethodHeaderContext,
    PackageDeclarationContext,
    PackageNameContext,
    PrimaryContext,
    SwitchBlockContext,
    TypeNameContext,
    UnannClassOrInterfaceTypeContext,
    UnannTypeVariableContext
} from "../java8/Java8Parser";
import {Java8Lexer} from "../java8/Java8Lexer";
import {Modifier, Variable, WorkerVariableScope} from "./variable-scope";
import {TerminalNode} from "antlr4ts/tree/TerminalNode";

export class AceJavaListener implements Java8ParserListener {
    imports: any[];
    typesRequired: any[];
    variables: Variable[];
    methods: Map<string, {
        type: string,
        name: string,
        modifier: Modifier,
        params: string[]
    }[]>;
    scope: {
        nextId: number;
        parent: any[];
        current: WorkerVariableScope;
    };
    className: string;
    packageName: string;
    interfaceNames: string[];
    parentClassName: string;

    constructor() {
        this.imports = [];
        this.typesRequired = [];
        this.variables = [];
        this.methods = new Map();
        this.scope = {
            current: {
                level: 0,
                id: 0,
                start: 0,
                stop: 0,
                parent: null
            },
            nextId: 1,
            parent: []
        };
        this.scope.parent.push(this.scope.current);
        this.packageName = null;
        this.className = null;
        this.parentClassName = 'Object';
        this.interfaceNames = [];
    }

    exitPackageDeclaration(ctx: PackageDeclarationContext) {
        for (let i = 0; i < ctx.children.length; i++) {
            const child = ctx.children[i];
            if (child instanceof PackageNameContext) {
                this.packageName = child.text;
                break;
            }
        }
    }

    exitImportDeclaration(ctx: ImportDeclarationContext) {
        let cImport = ctx.text.substr('import'.length);
        cImport = cImport.substr(0, cImport.length - 1);
        this.imports.push(cImport);
    }

    exitClassDeclaration(ctx: ClassDeclarationContext) {
        if (!(ctx.parent.parent instanceof CompilationUnitContext)) {
            return;
        }
        const normalClass = ctx.normalClassDeclaration();
        if (!normalClass) {
            console.warn("top most class isn't a normal class, not yet implemented");
        }
        this.className = normalClass.getToken(Java8Parser.Identifier, 0).text;
        if (normalClass.superclass()) {
            this.parentClassName = normalClass.superclass().classType().text;
        }

        if (normalClass.superinterfaces()) {
            this.interfaceNames = normalClass.superinterfaces().interfaceTypeList().interfaceType().map(x => x.text);
        }
    }

    exitTypeName(ctx: TypeNameContext) {
        this.typesRequired.push(ctx.text);
    }

    exitUnannClassOrInterfaceType(ctx: UnannClassOrInterfaceTypeContext) {
        this.typesRequired.push(ctx.text);
    }

    exitUnannTypeVariable(ctx: UnannTypeVariableContext) {
        this.typesRequired.push(ctx.Identifier().text);
    }

    exitClassType(ctx: ClassTypeContext) {
        this.typesRequired.push(ctx.text);
    }

    exitInterfaceType(ctx: InterfaceTypeContext) {
        this.typesRequired.push(ctx.text);
    }

    exitAmbiguousName(ctx: AmbiguousNameContext) {
        this.typesRequired.push(ctx.ruleContext.text);
    }

    exitPrimary(ctx: PrimaryContext) {
        const child = ctx.getChild(0);
        if (typeof child === 'undefined') return;
    }

    exitLocalVariableDeclaration(ctx: LocalVariableDeclarationContext) {
        this.handleVariableDeclaration(ctx, 'local')
    }

    exitFieldDeclaration(ctx: FieldDeclarationContext) {
        this.handleVariableDeclaration(ctx, 'field')
    }

    exitClassInstanceCreationExpression(ctx: ClassInstanceCreationExpressionContext) {
        if (ctx.children[0] instanceof TerminalNode) {
            this.typesRequired.push(ctx.Identifier().join("."));
        } else {
            this.typesRequired.push(ctx.Identifier()[0]);
        }
    }

    exitClassInstanceCreationExpression_lf_primary(ctx: ClassInstanceCreationExpression_lf_primaryContext) {
        this.typesRequired.push(ctx.Identifier().text);
    }

    exitClassInstanceCreationExpression_lfno_primary(ctx: ClassInstanceCreationExpression_lfno_primaryContext) {
        if (ctx.children[0] instanceof TerminalNode) {
            this.typesRequired.push(ctx.Identifier().join("."));
        } else {
            this.typesRequired.push(ctx.Identifier()[0]);
        }
    }

    handleVariableDeclaration(ctx: FieldDeclarationContext | LocalVariableDeclarationContext, decType: string) {
        const ids = ctx.variableDeclaratorList().variableDeclarator().map(x => x.variableDeclaratorId().text);
        const type = ctx.unannType().text;
        ids.forEach((id) => {
            this.variables.push({
                scope: this.scope.current,
                level: this.scope.current.level,
                type: type,
                id: id,
                decType: decType,
                line: ctx.stop.line
            });
        });
    }

    exitMethodDeclaration(ctx: MethodDeclarationContext) {
        const methodModifiers = ctx.methodModifier();
        const methodHeader: MethodHeaderContext = ctx.methodHeader();

        const securityModifierTokens = methodModifiers.filter(x => !(x instanceof AnnotationContext));
        let modifier: Modifier = "";
        if (securityModifierTokens.length > 0) {
            modifier = securityModifierTokens.pop().text as Modifier;
        }

        const type = methodHeader.result().text;
        let formalParameterTypes: string[] = [];
        if (methodHeader.methodDeclarator().formalParameterList()) {
            const lastFormalParameter = methodHeader.methodDeclarator().formalParameterList().lastFormalParameter();
            const formalParameters = methodHeader.methodDeclarator().formalParameterList().formalParameters();
            if (formalParameters) {
                formalParameterTypes = formalParameters.formalParameter().map((x) => x.unannType().text);
            }

            if (lastFormalParameter) {
                // Handle either a single last formal parameter which can be a varargs or a single formal parameter
                if (lastFormalParameter.formalParameter()) {
                    formalParameterTypes.push(lastFormalParameter.formalParameter().unannType().text);
                } else if (lastFormalParameter.unannType()) {
                    // This case is for a varargs, but we don't have a flag for that yet
                    const unannTypeContext = lastFormalParameter.unannType();
                    formalParameterTypes.push(unannTypeContext.text);
                }
            }
        }

        const name = methodHeader.methodDeclarator().Identifier().text;

        if (!this.methods.get(name)) {
            this.methods.set(name, []);
        }
        this.methods.get(name).push({
            type: type,
            name,
            modifier,
            params: formalParameterTypes
        });
    }

    // Constructor blocks are special blocks that can optionally call the super();
    enterConstructorBody(): void {
        this.enterBlock();
    }

    exitConstructorBody(ctx: ConstructorBodyContext): void {
        this._exitBlockScope(ctx.start.startIndex, ctx.stop.stopIndex);
    }

    // Class blocks are special
    enterClassBody(): void {
        this.enterBlock();
    }

    exitClassBody(ctx: ClassBodyContext): void {
        this._exitBlockScope(ctx.start.startIndex, ctx.stop.stopIndex);
    }

    // Same with class blocks, special parsing rules
    enterInterfaceBody(): void {
        this.enterBlock();
    }

    exitInterfaceBody(ctx: InterfaceBodyContext): void {
        this._exitBlockScope(ctx.start.startIndex, ctx.stop.stopIndex);
    }

    // Same with class blocks, special parsing rules
    enterEnumBody(): void {
        this.enterBlock();
    }

    exitEnumBody(ctx: EnumBodyContext): void {
        this._exitBlockScope(ctx.start.startIndex, ctx.stop.stopIndex);
    }

    // Same with class blocks, special parsing rules
    enterSwitchBlock(): void {
        this.enterBlock();
    }

    exitSwitchBlock(ctx: SwitchBlockContext): void {
        this._exitBlockScope(ctx.start.startIndex, ctx.stop.stopIndex);
    }

    // Same with class blocks, special parsing rules
    enterAnnotationTypeBody(): void {
        this.enterBlock();
    }

    exitAnnotationTypeBody(ctx: AnnotationTypeBodyContext): void {
        this._exitBlockScope(ctx.start.startIndex, ctx.stop.stopIndex);
    }

    enterBlock() {
        const parentScope = this.scope.current;
        this.scope.parent.push(parentScope);
        const currentId = this.scope.nextId;
        this.scope.nextId++;
        this.scope.current = {
            start: 0,
            stop: 0,
            id: currentId,
            level: this.scope.current.level + 1,
            parent: parentScope
        };
    }

    exitBlock(ctx: BlockContext) {
        this._exitBlockScope(ctx.start.startIndex, ctx.stop.stopIndex);
    }

    private _exitBlockScope(start: number, stop: number) {
        this.scope.current.start = start;
        this.scope.current.stop = stop;
        this.scope.current = this.scope.parent.pop();
    }

    processTokens(lexer: Java8Lexer) {
        lexer.reset();
        return lexer.getAllTokens().map((token, i) => ({
            column: token.charPositionInLine,
            line: token.line,
            start: token.startIndex,
            stop: token.stopIndex,
            type: lexer.vocabulary.getSymbolicName(token.type),
            text: token.text,
            index: i
        }));
    }
}