import * as antlr4 from "antlr4ts";
import {Java8Parser} from "../java8/Java8Parser";
import {Java8Lexer} from "../java8/Java8Lexer";
import {Mirror} from "./core/mirror";
import {Sender} from "./core/sender";
import {AnnotatingErrorListener} from "./annotating-error-listener";
import {AceJavaListener} from "./ace-java-listener";
import {VariableScope} from "./variable-scope";

export class JavaWorker extends Mirror {
    private $dialect: null;

    constructor(sender: any) {
        super(sender);
        super.setTimeout(200);
        this.$dialect = null;
    }

    onUpdate() {
        const value = this.doc.getValue();
        const annotations = this.inferErrorAnnotations(value);
        this.sender.emit('annotate', annotations);
    }

    static findMatchingScopeFromId(id: number, node: VariableScope): VariableScope {
        if (id === node.id) {
            return node;
        }
        for (const x of node.scopes) {
            const test = JavaWorker.findMatchingScopeFromId(id, x);
            if (test !== null) {
                return test;
            }
        }
        return null;
    }

    static findMatchingScope(scope_map: Map<number, VariableScope>, id: number, node: VariableScope): VariableScope {
        if (!scope_map.has(id)) {
            scope_map.set(id, JavaWorker.findMatchingScopeFromId(id, node))
        }
        return scope_map.get(id);
    }

    static processVariables(variablesToProcess: any[]): VariableScope {
        const rootScope: VariableScope = new VariableScope();
        const scopeCache = new Map<number, VariableScope>();
        for (const variable of variablesToProcess) {
            let testScope = variable.scope;
            let closetParent = null;
            const scopesThatNeedToBeAdded = [];
            const maxScopeLevel = testScope.level;
            for (let k = 0; k <= maxScopeLevel; k++) {
                closetParent = this.findMatchingScope(scopeCache, testScope.id, rootScope);
                if (closetParent === null) {
                    scopesThatNeedToBeAdded.push(testScope);
                    testScope = testScope.parent;
                } else {
                    // Speed up the loop, because we already know the rest of info
                    break;
                }
            }

            if (closetParent === null) {
                console.warn("java worker warning: closest parent is null")
                break;
            }

            for (const {start, stop, id, level} of scopesThatNeedToBeAdded.reverse()) {
                const newElement = new VariableScope(start, stop, id, level);
                closetParent.scopes.push(newElement);
                scopeCache.set(id, newElement);
                closetParent = newElement;
            }

            closetParent.variables.push({
                id: variable.id,
                type: variable.type,
                decType: variable.decType,
                line: variable.line,
                level: variable.scope.level,
                scope: variable.scope,
            });
        }

        return rootScope;
    }

     inferErrorAnnotations(input: string) {
        const stream = antlr4.CharStreams.fromString(input);
        const lexer = new Java8Lexer(stream);
        const tokens = new antlr4.CommonTokenStream(lexer);
        const parser = new Java8Parser(tokens);
        const parseResults: {
            annotations: {
                row: number,
                column: number,
                text: string,
                type: string,
            }[],
            imports: any,
            typesRequired: any,
            tokens: any,
            className: string,
            packageName: string,
            interfaces: any,
            parentClass: string,
            variables: VariableScope,
            methods: Map<string, any>
        } = {
            annotations: [],
            imports: null,
            typesRequired: null,
            tokens: null,
            className: null,
            packageName: null,
            interfaces: null,
            parentClass: null,
            variables: null,
            methods: null
        };
        const listener = new AnnotatingErrorListener(parseResults.annotations);
        parser.removeParseListeners();
        parser.removeErrorListeners();
        parser.addErrorListener(listener);
        const javaListener = new AceJavaListener();
         // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore seems like a bug somewhere in import land? these are the same type
        parser.addParseListener(javaListener);

        try {
            parser.compilationUnit();
        } catch (e) {
            // should already be logged via the results we are getting
        }
        parseResults.imports = javaListener.imports;
        parseResults.typesRequired = javaListener.typesRequired;
        parseResults.tokens = javaListener.processTokens(lexer);
        parseResults.className = javaListener.className;
        parseResults.packageName = javaListener.packageName;
        parseResults.interfaces = javaListener.interfaceNames;
        parseResults.parentClass = javaListener.parentClassName;
        parseResults.variables = JavaWorker.processVariables(javaListener.variables);
        parseResults.methods = javaListener.methods;
        return parseResults;
    }
}

//  Worker bindings for the main thread
let sender: Sender = null;
let main: any = null;

addEventListener('message', function (e) {
    const msg = e.data;
    if (msg.event && sender) {
        sender._signal(msg.event, msg.data);
    } else if (msg.command) {
        if (main[msg.command])
            main[msg.command].apply(main, msg.args);
        else
            throw new Error("Unknown command:" + msg.command);
    } else if (msg.init) {
        sender = new Sender();
        main = new JavaWorker(sender);
    }
});
